<template>
  <div id="transfer_pop" :class="themeName">
    <div class="transfer_container">
      <div class="_select">
        <el-select v-model="course" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.name"
            :value="item.value"
          ></el-option>
        </el-select>
        <p>(想要转移至的课程名称，剩余课时不低于转移课时)</p>
      </div>
      <footer>
        <button>设置完成</button>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      course: 1,
      options: [
        {
          name: "2020高考数学冲刺",
          value: 1
        },
        {
          name: "帝王成长超级班",
          value: 2
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
@import "@/styles/mixin.scss";
@import "@/styles/variables.scss";
#transfer_pop {
  padding: 40px;
}

.transfer_container {
  ._select {
    @include display(center) {
      flex-direction: column;
    }
    p {
      font-size: $font-size - 4px;
      color: #7e7e7e;
    }
    .el-select {
      width: 90%;
    }
  }
  footer {
    width: 100%;
    margin-top: 40px;
    button {
      @include buttonColor($type: "ge") {
        display: block;
        margin: auto;
      }
    }
  }
}
</style>



