<template>
  <div id="order_header">
    <div class="order_header_container">
      <div class="_top_left">
        <div class="_top_input">
          <ec-input
            @search="search"
            v-model="orderName"
            placeholder="教师 / 用户手机号 / 课程名"
          />
        </div>
      </div>
      <div class="btns_group">
        <div>
          <ec-button v-if="!isEdit_order" @click.native="edit">编辑</ec-button>
          <ec-button v-else @click.native="cancel">取消</ec-button>
        </div>
        <div>
          <ec-button>导出Excel</ec-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EcInput from "@/components/EducationInput";
import EcButton from "@/components/EducationButton";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      orderName: ""
    };
  },
  computed: {
    ...mapState("order", ["isEdit_order"])
  },
  components: {
    EcInput,
    EcButton
  },
  methods: {
    ...mapMutations("order", ["SET_EDIT_STATUS"]),
    search() {
      this.$emit("search", this.orderName);
    },
    edit() {
      this.SET_EDIT_STATUS(true);
    },
    cancel() {
      this.SET_EDIT_STATUS(false);
    }
  },
};
</script>

<style lang="scss" scoped>
@import "@/styles/mixin.scss";
@import "@/styles/variables.scss";
#order_header {
  .order_header_container {
    padding: 0 44px 0;
    // margin-bottom: 20px;
    @include display(space-between) {
      flex-wrap: wrap;
    }
    transition: all 1s;
    .btns_group {
      @include display;
      padding-bottom: 10px;
      & > div {
        margin-right: 25px;
      }
    }
    ._top_left {
      @include display;
      padding-bottom: 10px;
      // width: 50%;
      // ._top_input{
      //   width: 50%;
      // }
      ._top_select {
        // width: 55%;
        margin-left: 70px;
        @include display;
        .el-select {
          max-width: 150px;
        }
        span {
          display: inline-block;
          // margin-right: 20px;
          font-size: $font-size - 2px;
          width: 70px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}
</style>



