<template>
  <div id="refund_pop" :class="themeName">
    <div class="refund_container">
      <ul>
        <li>
          剩余课时：
          <span>25/30</span>
        </li>
        <li>
          剩余费用：
          <span class="money">2000元</span>
        </li>
      </ul>
      <div class="_input">
        <el-input type="number" v-model="money" placeholder="请输入退款金额"></el-input>
      </div>
      <div class="_input">
        <el-input v-model="reason" placeholder="请输入退款原因"></el-input>
      </div>
      <footer>
        <button>发起退款</button>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      money: "",
      reason: ""
    };
  }
};
</script>

<style lang="scss" scoped>
@import "@/styles/mixin.scss";
@import "@/styles/variables.scss";
#refund_pop {
  padding: 40px;
}
.refund_container {
  & > ul {
    @include display(space-between);
    li {
      .money {
        @include themify() {
          color: themed("money-color");
        }
      }
    }
  }
  ._input {
    margin: 20px 0;
  }
  footer {
    button {
      @include buttonColor($type: "ge") {
        display: block;
        margin: auto;
      }
    }
  }
}
</style>



